/* Global reset */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  /* Login Page Styling */
  body, html {
    height: 100%;
    background-repeat: no-repeat;
    background:black;
    position: relative;
  }
  
  .login-page {
    position: relative;
    height: 100vh;
    background-color: #000000;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    
  }
  
  #particles-js {
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 0;
  }
  
  .login-container {
    z-index: 1;
    width: 100%;
    max-width: 500px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .login-box {
    border: 1px solid black;
    background: rgba(48, 46, 45, 1);
    min-height: 250px;
    padding: 20px;
    z-index: 2;
    background-color: rgba(255, 255, 255, 0.85);
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    text-align: center;
  }
  
  .logo {
    margin-bottom: 20px;
  }
  
  .logo-caption {
    font-family: 'Poiret One', cursive;
    color: white;
    text-align: center;
    margin-bottom: 0px;
  }
  
  .tweak {
    color: #b1c900;
  }
  
  .controls .login-form {
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
  }
  
  .login-form label {
    margin-bottom: 8px;
    font-size: 1rem;
    color: #333;
  }
  
  .login-form input {
    padding: 10px;
    margin-bottom: 20px;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-size: 1rem;
  }
  
  .login-form button {
    background-color: #007bff;
    color: white;
    padding: 10px 20px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 1rem;
  }
  
  .login-form button:hover {
    background-color: #0056b3;
  }
  
  .forgot-password {
    display: block;
    margin-top: 10px;
    color: #007bff;
    text-decoration: none;
  }
  
  .forgot-password:hover {
    text-decoration: underline;
  }
  